@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2"),
    url("../fonts/opensans.woff");
  font-weight: 400;
  font-style: normal;

  font-display: swap;
  src: local("Open Sans");
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2"),
    url("../fonts/opensansbold.woff");
  font-weight: 700;
  font-style: normal;

  font-display: swap;
  src: local("Oswald");
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
