html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;

  /*временно для моб версии*/

  /*временно для моб версии*/

  margin: 0 auto;

  font-family: $main-font;
  font-size: 15px;
  font-weight: 400;
  color: $font-color-grey;
  text-transform: none;
  line-height: 24px;

  background-color: $color-white;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

